import React, { useState, useEffect } from 'react';
import { gapi } from "gapi-script";

import 'bootstrap/dist/css/bootstrap.min.css';


const CLIENT_ID =
    "420580046683-frn4robcktpb5fujdvrjifp8nc08e0c0.apps.googleusercontent.com";
const API_KEY = "AIzaSyDpkfciU5DXNmasvI3KqUiQbRk7AP26uG0";
const SPREADSHEET_ID = "1sV6zLKlzV0tIS7VEpfLO_UvkJgTu6rOzmyTg68SZYc8";

function RegisForm() {

    const [data, setData] = useState({
        Name: "",
        Email: "",
        PhoneNumber: "",
        TypeStudent: "",
        Specialized: "",
    });

    useEffect(() => {
        function start() {
            gapi.client
                .init({
                    apiKey: API_KEY,
                    clientId: CLIENT_ID,
                    discoveryDocs: [
                        "https://sheets.googleapis.com/$discovery/rest?version=v4",
                    ],
                    scope:
                        "https://www.googleapis.com/auth/drive https://www.googleapis.com/auth/drive.file https://www.googleapis.com/auth/spreadsheets",
                })
                .then(
                    () => {
                        console.log("GAPI client loaded for API");
                    },
                    (error) => {
                        console.error("Error loading GAPI client for API", error);
                    }
                );
        }

        gapi.load("client:auth2", start);
    }, []);

    const handleChange = (e) => {
        setData({ ...data, [e.target.name]: e.target.value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        const currentDate = new Date();
        const formattedDate = `${currentDate.getFullYear()}-${currentDate.getMonth() + 1
            }-${currentDate.getDate()}`;
        const newData = { ...data, Date: formattedDate };
        await authenticate();
        await execute(newData);
    };

    const authenticate = async () => {
        gapi.auth2
            .getAuthInstance()
            .signIn()
            .then(
                () => {
                    console.log("Sign-in successful");
                },
                (error) => {
                    console.error("Error signing in", error);
                }
            );
    };

    const execute = (newData) => {
        const spreedData = Object.values(newData);
        console.log(spreedData);
        gapi.client.sheets.spreadsheets.values
            .append({
                spreadsheetId: SPREADSHEET_ID,
                range: "Test!A:F",
                insertDataOption: "INSERT_ROWS",
                valueInputOption: "USER_ENTERED",
                resource: {
                    values: [spreedData],
                },
            })
            .then(
                (response) => {
                    console.log("Response", response);
                },
                (error) => {
                    console.error("Execute error", error);
                }
            );
    };

    return (
      <div>
      <section className="">
        <div className="pt-4 pb-4 col-m-6 col-md">
          <div className="p-5 rounded shadow bg-white">
            <h3 className="text-center" style={{ fontSize: '4.0rem', color: '#F47E24' }}>NG K HC</h3>
            <h3 className="mb-5 text-center" style={{ fontSize: '1.5rem' }}>Hy tham gia v tri nghim ti iSPACE</h3>
            <form onSubmit={handleSubmit}>
              <div className="row">
              <div className="mb-3 col-md-6">
                <input
                  type="text"
                  name="Name"
                  placeholder="H Tn Bn *"
                  required
                  className="form-control"
                  style={{ height: '50px', fontSize: '1.5rem' }}
                  onChange={handleChange}
                />
              </div>
              <div className="mb-3 col-md-6">
                <input
                  type="email"
                  name="Email"
                  placeholder="Email *"
                  required
                  className="form-control"
                  style={{ height: '50px', fontSize: '1.5rem' }}
                  onChange={handleChange}
                />
              </div>
              </div>
              <div className="mb-3">
                <input
                  type="text"
                  name="PhoneNumber"
                  placeholder="S in thoi *"
                  required
                  className="form-control"
                  style={{ height: '50px', fontSize: '1.5rem' }}
                  onChange={handleChange}
                />
              </div>
              <div className="mb-3">
                <select
                  name="TypeStudent"
                  required
                  className="form-control"
                  style={{ height: '50px', fontSize: '1.5rem' }}
                  onChange={handleChange}
                >
                  <option value="">-----i tng-----</option>
                  <option value="HS ang hc lp 12 (ng k gi ch)">
                    HS ang hc lp 12 (ng k gi ch)
                  </option>
                  <option value="HS tt nghip THPT/hon thnh chng trnh lp 12">
                    HS tt nghip THPT/hon thnh chng trnh lp 12
                  </option>
                  <option value="Sinh Vin">Sinh Vin</option>
                  <option value="Ngi i lm">Ngi i lm</option>
                </select>
              </div>
              <div className="mb-3">
                <select
                  name="Specialized"
                  required
                  className="form-control"
                  style={{ height: '50px', fontSize: '1.5rem' }}
                  onChange={handleChange}
                >
                  <option value="">----- Chng trnh hc -----</option>
                  <option value="An ninh mng">An ninh mng</option>
                  <option value="Pht trin phn mm">Pht trin phn mm</option>
                  <option value="Thit k  ha">Thit k  ha</option>
                  <option value="Qun tr mng">Qun tr mng</option>
                  <option value="Thng mi in t">Thng mi in t</option>
                </select>
              </div>
              <button
                type="submit"
                className="btn btn-primary w-100"
                style={{ height: '50px', fontSize: '1.5rem' }}
              >
                GI NGAY
              </button>
            </form>
          </div>
        </div>
      </section>
    </div>
    );
}

export default RegisForm;
